@import url(https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
form.navigation.form-inline {
    display: flex;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Mulish', sans-serif; 
}

.navbar{
    background-color: #fff  !important;
    transition: 0.10s;
}
.logo{
    font-size: 1.4rem !important;
    color: #565387 !important;
}
.navbar a{
    font-size: 1rem;
    text-transform: capitalize;
    text-decoration: none ;
    transition: 0.10s;
    color: rgb(0 0 0 / 75%);
}

.navigat:hover{
    font-weight: bold;
    border-bottom: 1px solid  #565387;
    transition: 0.10s;
}

.navbar a:hover{
    color: #3498db !important;
    transition: 0.10s;
}

@media(max-width:991px){
    .logo{
        font-size: 1rem;
    }
    #header{
        height: 80vh;
        text-align: center;
    }
    #header .header-img{
        text-align: center;
    }
}
@media(max-width:768px){

    #header{
        margin-top: 20px;
    }
    #header h1{
        font-size: 28px;
    }
    #header h2{
        font-size: 18px;
        margin-bottom: 30px;
    }
    #header .header-img{
        text-align: center;
    }

} 
/* ==============================================================================================
# Main Landing Section
============================================================================================== */


#header{
    width: 100%;
    height: 80vh;
    margin-top: 70px;
}

#header h1{
    margin: 0 0 10px 0;
    font-size: 36px;
    font-weight: 700;
    color: black;
}

#header .brand_name{
    color: #3498db;
}

#header h2{
    color: #484848;
    font-size: 20px;
}

#header .get_started{
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 1px;
    display: inline-block;
    padding: 10px 30px;
    border-radius: 50px;
    transition: 0.5s;
    color: #3498db;
    border: 2px solid #3498db;
    text-decoration: none;
    margin-top: 20px;
}
#header .get_started:hover{
    background-color: #3498db;
    color: #fff;
}

.header-img{
    text-align: right;
}

#animated{
    animation: up-down 1.5s ease-in-out infinite alternate-reverse both;
    /* animation-name: updown;
    animation-duration: 5s; */
}

@keyframes up-down{
    0%{
        color: brown;
        transform:translateY(10px)
        /* transform-origin: 10% 40%; */
    }
    100%{
        color: #fff;
        transform:translateY(-20px)
        /* transform-origin: 40% 80%; */
    }
}

.service_img{
    height: 320px;
}

.from_valid.form-row {
    display: flex;
}

.from_class.form-group.col-md-4 {
    margin-right: 40px;
}

img.error_img {
    height: 765px;
}

